.second-carousel {

    .slick-next,
    .slick-prev {
        display: none !important;
    }

    .carousel-item {
        width: 359px !important;
        height: 238px !important;
        display: flex !important;
        flex-direction: column;
        justify-content: space-between;
        padding: 24px 30px 41px;
        align-items: flex-start;
        text-align: left;
        background: #ccc;
        background-size: cover;
        &.first{
            background:url(./images/first-image.png) no-repeat,url(./images/first-bacground.png) no-repeat ;
            background-position: right top 50px, center;
        }
        &.second{
            background: url(./images/second-image.png) no-repeat ,url(./images/second-bacground.png) no-repeat;
            background-position: right top , center;
        }
        &.third{
            background: url(./images/third-image.png) no-repeat,url(./images/third-bacgroudn.png);
            background-position: right top 25px, center;
        }
        h2 {
            width: 170px;
            font-family: 'Gilroy bold', sans-serif;
            font-size: 23px;
            line-height: 26px;
            color: #000000;
        }

        a {
            text-decoration: none;
            font-size: 15px;
            line-height: 18px;
            font-family: 'Gilroy normal';
            color: #4A4A4A;
            position: relative;
            &::after{
                content: ' ';
                background: url(./images/angel-left.svg) no-repeat;
                position: absolute;
                width: 5.5px;
                height: 10px;
                top: 0;
                bottom: 0;
                right: -20px;
                margin: auto 0;
            }
        }
    }
}