.carousel-item {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    .img-wrapper {
        width: 75px;
        height: 75px;

        img {
            width: 100%;
            height: 100%;
        }
    }

    p {
        font-family: 'Gilroy bold', sans-serif;
        font-weight: bold;
        font-size: 14px;
        color: #4A4A4A;
    }
}