.page-wrapper {
    width: 100%;

    .post-wrapper {
        margin-top: 50px;

        .post-type {
            margin-top: 30px;
            .top {
                .post-status {
                    font-family: 'Gilroy bold', sans-serif;
                    font-weight: bold;
                    font-size: 24px;
                    color: #9B9B9B;
                    background: transparent;
                    border: none;
                    outline: none;
                    transition: all ease 0.5s;
    
                    &:first-of-type,
                    &:nth-of-type(2) {
                        margin-right: 77px;
                    }
    
                    &.active {
                        color: #4E72CA;
                    }
                }
            }
            .bottom {
                margin-top: 12px;
                position: relative;
    
                .line {
                    width: 100%;
                    height: 1px;
                    background: #D5D5D5;
                }
    
                .active-new {
                    position: absolute;
                    left: 50px;
                    top: 0;
                    bottom: 0;
                    margin: auto 0;
                    width: 130px;
                    height: 3px;
                    transition: all ease 0.5s;
                    background: transparent;
    
                    &.active {
                        background: #4E72CA;
                    }
                }
    
                .active-best {
                    position: absolute;
                    left: 350px;
                    top: 0;
                    bottom: 0;
                    margin: auto 0;
                    width: 150px;
                    height: 3px;
                    transition: all ease 0.5s;
                    background: transparent;
    
                    &.active {
                        background: #4E72CA;
                    }
                }
            }
        }

        .posts-wrapper {
            margin-top: 30px;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
        }

        .post-wrapper-bottom {
            text-align: center;

            .line {
                width: 100%;
                height: 2px;
                background: rgba(182, 202, 251, 0.606827);
                margin: 20px 0;
            }

            a {
                font-family: 'Gilroy normal', sans-serif;
                font-weight: 500;
                font-size: 15px;
                color: #4E72CA;
                position: relative;
                text-decoration: none;

                &::after {
                    content: '';
                    background: url(../../assets/images/icons/down.svg) no-repeat;
                    width: 10px;
                    background-size: cover;
                    height: 5.5px;
                    position: absolute;
                    right: -20px;
                    top: 0;
                    bottom: 0;
                    margin: auto 0;
                }
            }
        }
    }

    .second-carousel-wrapper {
        margin-top: 50px;
        padding: 50px 0;
        background: rgba(182, 202, 251, 0.159986);
        box-shadow: inset 0px 21px 13px rgba(182, 202, 251, 0.225402);
    }
}

