.slick-slider {
        padding-top: 30px;

        .slick-prev {
            background: #FFFFFF;
            box-shadow: 1px 3px 6px rgba(0, 0, 0, 0.129331);
            width: 34px;
            height: 34px;
            border-radius: 50%;

            &::before {
                content: '';
                background: url(../../containers/Carousel/CarouselImage/Prev-btn.svg) no-repeat;
                height: 14px;
                width: 25px;
                position: absolute;
                right: 0;
                top: 0;
                bottom: 0;
                margin: auto 0;
            }
        }

        .slick-next {
            background: #FFFFFF;
            box-shadow: 1px 3px 6px rgba(0, 0, 0, 0.129331);
            width: 34px;
            height: 34px;
            border-radius: 50%;

            &::before {
                content: '';
                background: url(../../containers/Carousel/CarouselImage/Prev-btn.svg) no-repeat;
                height: 14px;
                width: 25px;
                position: absolute;
                left: 0;
                top: 0;
                transform: rotate(180deg);
                bottom: 0;
                margin: auto 0;
            }
        }
    }